$icon-font-path: '../fonts/';

// bower:scss
// endbower

$gray : #ddd;
$primary-color : #1de9b6;


@import 'utilities';

body{
	//margin-top: 50px;
	//padding-top:70px;
	.navbar{
		margin:0px;
		border-bottom: 1px solid lightgray;
		.navbar-right{
			margin:0px !important;
		}
	}
	section{
		&#home{
			height: 100vh;
			position: relative;
			form{
				width:100%;
			}
		}
		&#jobs{
		
		}
	}
	.sub-header{
		h1{
			margin:0px;
		}
		
	}
	aside{
		&.toolbar{
			background:url('/images/view-job-bg.jpg');
			background-size:cover;
			padding:0px;
			background-position: center bottom;
			.row{
				background-color:rgba(black,0.7);
				margin:0px;
				.title{
					padding : 15px 0;
					margin:0px;
					position:relative;
					img.circle{
						width: 100px;
					}
					.location{
						font-size:13px;
						margin-top:15px;
						i{
							font-size:inherit;
						}
					}
					h1{
						margin: 0px;
						font-size: 30px;
					}
					.chips{
						margin-top: 15px;
						border: none !important;
						margin-bottom: 0px;
						min-height: 0px;
						.chip{
							background: transparent;
							border: 2px solid lightgray;
							margin:0px;
							line-height: 26px;
							a{
								color: lightgray;
							}
						}
					}
					.info{
						ul{
							margin: 10px 0;
							border:0px;
							li{
								text-transform:uppercase;
								margin-right:10px;
								display: inline-block;
								background:transparent;
								border:0px;
								font-size:10px;
								min-height:initial;
								i{
									font-size:inherit;
									margin-right:5px;
									vertical-align: middle;
								}
							}
						}
					}
				}
				span{
					line-height: 33px;
					i{
					
					}
				}
			}
		}
		&.sidebar{
			font-size: 12px;
			.panel{
				.panel-body{
					.header{
						padding-bottom: 10px;
						margin-bottom: 10px;
					}
					h3{
						line-height: 22px;
						padding: 0px;
						margin: 0px;
						font-weight: bold;
						font-size:inherit;
						text-transform: uppercase;
					}
					dl{
						&.row{
							margin-bottom:0px;
							text-transform: capitalize;
							color:gray;
							vertical-align:middle;
							dt{
								color : gray;
								line-height:30px;
							}
							dd{
								color : $primary-color;
								line-height: 30px;
								span{
									color:gray;
									font-style: italic;
									select,input{
										&.form-control{
											margin-bottom: 10px;
											text-transform: capitalize;
										}
									}
								}
							}
						}
					}
				}
			}
		}
		&.inset{
		
		}
	}
	main{
		#content{
			position:relative;
			.jobs{
				li{
					padding-right: 10px;
					padding-left: 10px;
					.panel{
						position:relative;
						border: 1px solid lightgray;
						.panel-body{
							h4{
								font-size:14px;
								font-weight:bold;
							}
							small{
								color:gray;
							}
						}
					}
				}
			}
			.card{
				.panel-heading{
					h1{
						font-weight:bold;
						text-transform:uppercase;
						margin-top:0px;
					}
				}
				.card-content{
					.job_details{
						li{
							h2{
								font-size:12px;
								font-weight:bold;
								text-transform:uppercase;
								margin-top:0px;
							}
						}
					}
				}
			}
		}
		.view-asset{
			h1,h6{
				font-weight:300;
			}
		}
	}
}
